@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,300italic,regular,italic,500,500italic,700,700italic);
@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
html {
  font-family: Ubuntu, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

a {
  color: #fff;
  text-decoration: none;
}

.container {
  max-width: 1600px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.hidden {
  display: none;
}

.title {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  color: #333;
  font-weight: 900;
  font-size: 36px;
  line-height: 130%;
  text-transform: uppercase;
}

.btn a {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  background: inherit;
  border: none;
  outline: none;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  color: #023E8A;
}
.btn a svg {
  width: 60px;
  height: 35px;
}

.burger {
  flex-basis: 33.3%;
  text-align: left;
  padding: 0;
  margin: 0;
  display: none;
  position: relative;
  border: none;
  background: none;
  height: 21px;
  width: 30px;
  cursor: pointer;
}

.burger-line {
  width: 30px;
  height: 3px;
  background-color: #fff;
  transition: 0.3s all ease;
}

.burger-line:nth-child(2) {
  width: 20px;
}

.top-line {
  position: absolute;
  top: 0;
  left: 0;
}

.mid-line {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}

.bottom-line {
  position: absolute;
  left: 0;
  bottom: 0;
}

.burger.active > .top-line {
  transform: translateY(8px) rotate(45deg);
  transition: 0.3s all ease;
}

.burger.active > .mid-line {
  opacity: 0;
  transition: 0.3s all ease;
}

.burger.active > .bottom-line {
  transform: translateY(-10px) rotate(-45deg);
  transition: 0.3s all ease;
}

